import React, { useEffect } from "react"
import DefaultAvatar from "src/assets/images/avatar.jpg"

export default function MyUIAvatar(props) {
  const { alt, src, className, style } = props
  const [image, setImage] = React.useState(src)
  const onLoadImageError = () => {
    setImage(DefaultAvatar)
  }
  useEffect(() => {
    setImage(src)
  }, [src])
  return (
    <img
      src={image || DefaultAvatar}
      alt={alt || ""}
      className={className}
      style={style}
      onError={() => onLoadImageError()}
    />
  )
}
